.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #4FBF26;
}

.navbar-fnt {
  color: #4FBF26;
}

.navbar-fnt:hover {
  color: lightgreen;
}

.subtitle {
  padding-top: 10px;
  padding-bottom: 10px;
}

.footer-icon {
  padding: 5px;
  cursor: pointer;
  color: #4FBF26;
}

.footer-icon:hover {
  color: lightgreen;
}

.horizontal-line {
  border-top: 1px solid gray;
  margin-left: 12;
  margin-right: 12;
  padding-top: 10;
  padding-bottom: 10;
}

.btn-custom {
  background-color: #4FBF26;
  color: white;
}

.nav-link {
  color: #4FBF26 !important;
}

.nav-pills .nav-link.active {
  background-color: #4FBF26 !important;
  color: white !important;
}

h5 {
  margin-bottom: 0;
}

p {
  font-size: 18px;
}

a {
  color: #4FBF26;
}

a:hover {
  color: lightgreen;
}

